//FLEXBOX MIXIN
@mixin flexbox($justify-content, $align-items) {
    display: flex;
    justify-content: $justify-content;
    align-items: $align-items; }

//MIEDIA QUERIES MIXIN
@mixin mQp($min-width, $orientation) {
    @media (min-width: $min-width) and (orientation: $orientation) {
        @content; } }

@mixin mQ($min-width) {
    @media (min-width: $min-width) {
        @content; } }
